body {
  margin: 0;
  padding: 0;
  font-family: Microsoft JhengHei;
  background-image:url('./wood-background.jpg')
}

textarea{
	width:190px;
	min-height:140px;
	resize:none;
	background-color:rgba(0,0,0,0);
	font-size:16px;
	font-family: Microsoft JhengHei;
	padding:5px;
	border: none;
    outline:none;
}

ul{
	list-style-type: none;
	padding:0px;
	font-size:16px;
	margin:0px;
}

.title{
	text-align:center;
	font-family: 'Handlee', cursive;
	font-size:4rem;
	margin:0px;
	color:#1A1A1A;
	margin-top:10px;
}

.drag{
	width:100%;
	height:50px;
}

.drag-yellow{
	background-color: #FFD306;
}

.drag-red{
	background-color:#FF2D2D;
}

.to-do-content{
	width:180px;
	min-height:130px;
	background-color:#ff7575;
	padding:10px;
}

.board{
  width:30vw;
  height:300px;
  border:1px solid #000000;
  margin:5px;
}

.flex{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.wrap{
	-ms-flex-wrap:wrap;
	    flex-wrap:wrap;
}

.btn{
  padding:8px 12px;
  margin:10px;
  max-width:160px;
  text-align:center;
  cursor:pointer;
  background-color: #3C3C3C;
  font-size:20px;
  color:white;
  border:0px;
}

.note{
	position:relative;
	width:200px;
	min-height:200px;
	border:0px;
	background-color:#FFE153;
	-webkit-box-shadow: 1px 1px 2px gray;
	        box-shadow: 1px 1px 2px gray;
}

.to-do-list{
	position:relative;
	width:200px;
	min-height:200px;
	border:0px; 
	-webkit-box-shadow: 1px 1px 2px gray; 
	        box-shadow: 1px 1px 2px gray;
}

.delete-btn{
	cursor:pointer;
	position:absolute;
	top:5px;
	right:5px;
	color:#4D4D4D;
}

.delete-btn:hover{
	color:#000000;
}

.show-area{
	position:relative;
	width:200px;
	min-height:200px;
	margin:10px;
}

.add-to-do-item{
	border:none;
	border-bottom:1px solid black;
	background-color:rgba(0,0,0,0);
	outline:none;
	width:160px;
	font-size:16px;
	margin-top:10px;
	font-family: Microsoft JhengHei;
}

.icon-small{
	font-size:16px;
}

.submit-btn{
	cursor:pointer;
	background-color: rgba(0,0,0,0);
	border:none;
	outline:none;
	padding:0px;
}

.ml-5px{
	margin-left:5px;
}

.add-title{
	margin-top:22px;
	padding-left:5px;
	background-color: rgba(0,0,0,0);
	border:none;
	font-size:20px;
	font-weight: bold;
	outline:none;
	font-family: Microsoft JhengHei;
}

.add-title::-webkit-input-placeholder{
	color:#3C3C3C;
}

.add-title:-ms-input-placeholder{
	color:#3C3C3C;
}

.add-title::-ms-input-placeholder{
	color:#3C3C3C;
}

.add-title::placeholder{
	color:#3C3C3C;
}

.check{
	vertical-align: middle;
	cursor:pointer;
	background-color:rgba(0,0,0,0);
	border:1px solid #000;
	visibility:hidden;
}

ul li{
	vertical-align: bottom;
}

ul li span{
	width: 170px;
}

.hide{
	visibility: hidden;
}

.flex-start{
	display:flexbox;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

ul li:hover .hide{
	visibility:visible;
}






